<template>
	<div class="shopCart" id="shopCart">
		<div v-if="listLen<=0">
			<nothing></nothing>
		</div>
		
		<footertabbar></footertabbar>
	</div>
</template>

<script>
	import footertabbar from "../components/footer"
	import nothing from "../components/nothing"
	export default{
		name:"shopCart",
		data(){
			return {
				listLen:0
			}
		},
		components: { footertabbar, nothing}
	}
</script>

<style>
</style>


