<template>
	<div id="order">
		<div v-if="listLen<=0">
			<nothing></nothing>
		</div>
		<yd-list theme="4">
	        <yd-list-item v-for="item,key in list" :key="key" v-if="item.state==state||state==''">
	            <img slot="img" :src="item.img">
	            <span slot="title">{{item.title}}</span>
	            <yd-list-other slot="other">
	                <div>
	                    <span class="demo-list-price"><em>¥</em>{{item.price}}</span>
	                    <span class="demo-list-del-price">¥{{item.w_price}}</span>
	                </div>
	                <div class="state">
	                	<span v-if="item.state==1">已发货</span>
	                	<span v-if="item.state==2">待发货</span>
	                	<span v-if="item.state==3">已签收</span>
	                </div>
	            </yd-list-other>
	        </yd-list-item>
	    </yd-list>
	</div>
</template>

<script>
	import nothing from "../../components/nothing"
	export default{
		name:"order",
		data(){
			return {
				state : "",
				listLen:0,
				list: [
                    {img: "//img1.shikee.com/try/2016/06/23/14381920926024616259.jpg", title: "春夏爆款-收腰显瘦", price: 156.23, w_price: 89.36,state:"1"},
                    {img: "//img1.shikee.com/try/2016/06/21/10172020923917672923.jpg", title: "春夏爆款-韩版", price: 256.23, w_price: 89.36,state:"2"},
                    {img: "//img1.shikee.com/try/2016/06/23/15395220917905380014.jpg", title: "春夏爆款-潮流前线", price: 356.23, w_price: 89.36,state:"1"},
                    {img: "//img1.shikee.com/try/2016/06/25/14244120933639105658.jpg", title: "春夏爆款-收腰显瘦", price: 456.23, w_price: 89.36,state:"3"},
                    {img: "//img1.shikee.com/try/2016/06/26/12365720933909085511.jpg", title: "春夏爆款-时尚主流", price: 556.23, w_price: 89.36,state:"2"}
                ]
			}
		},
		mounted:function(){
			this.listLen = this.list.length;
			if(this.$route.query.state){
				this.state = this.$route.query.state;
			}
		},
		components: {nothing}
	}
</script>

<style>
</style>


