<template>
	<div id="newAddress">
		<yd-cell-group>
	        <yd-cell-item>
	            <span slot="left">联系人：</span>
	            <input slot="right" type="text" placeholder="姓名" v-model="name">
	        </yd-cell-item>
	        <yd-cell-item>
	            <span slot="left">手机号：</span>
	            <input slot="right" type="number" placeholder="请输入手机号" v-model="phone">
	        </yd-cell-item>
    
	        <yd-cell-item arrow>
	            <span slot="left">所在地区：</span>
	            <input slot="right" type="text" @click.stop="show1 = true" v-model="address" readonly placeholder="请选择收货地址">
	        </yd-cell-item>
	        <yd-cityselect v-model="show1" :callback="result1" :items="district"></yd-cityselect>
	        <yd-cell-item>
	            <span slot="left">详细地址：</span>
	            <input slot="right" type="text" placeholder="街道、楼牌号" v-model="street">
	        </yd-cell-item>
	    </yd-cell-group>
	    
	    <router-link class="add_address" :to="{name:'address'}" @click.native="saveaddress">保存地址</router-link>
        
    </div>
</template>

<script>
	// 城市三级联动
	import District from 'ydui-district/dist/jd_province_city_area_id';
	export default{
		name:"newaddress",
		data(){
			return {
				name:"",
				phone:"",
				street:"",
				show1: false,
                address: '',
                district: District
			}
		},
		methods:{
			result1:function(data){
				this.address = data.itemName1+" "+data.itemName2+" "+data.itemName3;
			},
			saveaddress:function(){
				if(window.localStorage.addressList){
					
					var addressArry = JSON.parse(window.localStorage.addressList);
					var obj = {
						name:this.name,
						phone:this.phone,
						street:this.street,
						address:this.address
					};
					addressArry.push(obj)
					console.log(addressArry)
					window.localStorage.addressList = JSON.stringify(addressArry);
				}else{
					
					var addressArry = [];
					var obj = {
						name:this.name,
						phone:this.phone,
						street:this.street,
						address:this.address
					};
					addressArry.push(obj)
					console.log(addressArry)
					window.localStorage.addressList = JSON.stringify(addressArry);
				}
//				console.log(this.name+"--"+this.phone+"--"+this.street+"--"+this.address)
			}
		}
	}
</script>

<style>
</style>


