import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import index from '@/page/index'
import shopCart from '@/page/shopCart'
import market from '@/page/market'
import myCenter from '@/page/myCenter'
import details from '@/page/goods/details'
import order from '@/page/goods/order'
import collect from '@/page/goods/collect'
import address from '@/page/goods/address'
import newAddress from '@/page/goods/newAddress'
import login_regiser from '@/page/login_regiser'
import login from '@/page/member/login'
import register from '@/page/member/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/page/index',
      name: 'index',
      component: index
    },
    {
    	path: '/page/shopCart',
    	name: 'shopCart',
    	component: shopCart
    },
    {
    	path: '/page/market',
    	name: 'market',
    	component: market
    },
    {
    	path: '/page/myCenter',
    	name: 'myCenter',
    	component: myCenter
    },
    {
    	path: '/page/goods/details',
    	name: 'details',
    	component: details
    },
    {
    	path: '/page/goods/order',
    	name: 'order',
    	component: order
    },
    {
    	path: '/page/goods/collect',
    	name: 'collect',
    	component: collect
    },
    {
    	path: '/page/goods/address',
    	name: 'address',
    	component: address
    },
    {
    	path: '/page/goods/newAddress',
    	name: 'newAddress',
    	component: newAddress
    },
    {
    	path: '/',
    	name: 'login_regiser',
    	component: login_regiser,
    	redirect: '/page/member/login',
	    children: [
				{
					path: '/page/member/login',
					name: 'login',
					component: login
				},
				{
					path: '/page/member/register',
					name: 'register',
					component: register
				}
			]
    }
  ]
})



