<template>
	<div class="market" id="market">
		<template class="tab">
		    <yd-tab v-model="selected" :callback="fn" :prevent-default="false" :item-click="itemClick">
		        <yd-tab-panel v-for="item,key in items" :key="key" :label="item.label"></yd-tab-panel>
		    </yd-tab>
		</template>
		<template>
		    <yd-list theme="2">
		        <yd-list-item v-for="goods,key in lists" :key="key" type="link" :href="{name: 'details',query:{'goodsId':key}}">
		        	<img slot="img" :src="goods.img">
	        		<span slot="title">{{goods.title}}</span>
	        		<yd-list-other slot="other">
	        			<div>
	        				<span class="demo-list-del-price">¥{{goods.price}}</span>
	        				<!--<span class="demo-list-price"><em>¥</em>{{goods.price}}</span>-->
	        			</div>
	        			<div>热销中</div>
	        		</yd-list-other>
		        </yd-list-item>
		    </yd-list>
		</template>
		<footertabbar></footertabbar>
	</div>
</template>

<script>
	import footertabbar from "../components/footer"
	export default {
        data() {
            return {
                selected: 0,
                items: [
                    {label: '水果'},
                    {label: '女装'},
                    {label: '男装'},
                    {label: '其他'}
                ],
                lists:[]
            }
        },
        mounted:function(){
        	let _this = this;
            _this.$dialog.loading.open('数据加载中');
            
        	this.$ajax({
            	method: 'get',
				url: '/static/data/market/0.txt'
            })
            .then(function(data){
            	if(data.status == "200"){
            		setTimeout(() => {
            			_this.lists = data.data.goods;
	                    _this.$dialog.loading.close();
	                }, 1000);
            	}
            })
            .catch(function(error){
            	console.log(error)
            })
            
        },
        methods: {
            fn(label, key) {
            	
            },
            itemClick(key) {
            	let _this = this;
                _this.$dialog.loading.open('数据加载中');
                
                this.$ajax({
                	method: 'get',
					url: '/static/data/market/'+key+'.txt'
                })
                .then(function(data){
                	if(data.status == "200"){
                		setTimeout(() => {
                			_this.lists = data.data.goods;
		                    _this.selected = key;
		                    _this.$dialog.loading.close();
		                }, 1000);
                	}
                })
                .catch(function(error){
                	console.log(error)
                })
                
            }
        },
        components: { footertabbar }
    }
</script>

<style>
</style>


