<template>
	<div id="addresslist">
		<div v-if="listLen<=0">
			<nothing></nothing>
		</div>
		
		<div class="addressList" v-for="address in list">
			<div class="top">
				<p><span class="name">{{address.name}}</span><span class="phone">{{address.phone}}</span></p>
			</div>
			<div class="mid">街道：{{address.street}}</div>
			<div class="bottom">地址：{{address.address}}</div>
		</div>
		<router-link class="add_address" :to="{name:'newAddress'}">新增地址</router-link>
		
		
	</div>
</template>

<script>
	
	import nothing from "../../components/nothing"
	export default{
		name:"addresslist",
		data(){
			return {
				list:[],
				listLen:0
			}
		},
		mounted:function(){
			if(window.localStorage.addressList){
				
				this.list = JSON.parse(window.localStorage.addressList)
				this.listLen =this.list.length
			}
		},
		components: {nothing}
	}
</script>

<style>
</style>


