<template>
	<div id="detail" class="detail">这是第{{key}}个商品的详情页</div>
</template>

<script>
	export default{
		name: "detail",
		data(){
			return{
				key:0,
				keyNum:''
			}
		},
        mounted:function(){
        	this.keyNum = this.$route.query.goodsId;
			this.key = Number(this.$route.query.goodsId)+1;
			this.$dialog.alert({mes: '您查看的商品ID为:'+this.keyNum+' 所属列表的第'+this.key+'个'});
		}
	}
</script>

<style>
</style>


