<template>
	<div id="collect">
		<div v-if="listLen<=0">
			<nothing></nothing>
		</div>
	</div>
</template>

<script>
	import nothing from "../../components/nothing"
	export default{
		name:"collect",
		data(){
			return {
				listLen:0
			}
		},
		components: {nothing}
	}
</script>

<style>
</style>


