<template>
	<div class="login">
		<div class="form-box">
			<yd-cell-group>
		        <yd-cell-item>
		            <span slot="left">用户名：</span>
		            <yd-input slot="right" required v-model="Nameinput" max="20" placeholder="请输入用户名"></yd-input>
		        </yd-cell-item>
		        <yd-cell-item>
		            <span slot="left">密&#12288;码：</span>
		            <yd-input slot="right" type="password" v-model="Passwdinput" placeholder="请输入密码"></yd-input>
		        </yd-cell-item>
		    </yd-cell-group>
		    <yd-button-group class="loginBth">
            	<yd-button size="large" @click.native="login">注册并登录</yd-button>
      		</yd-button-group>
		</div>
	</div>
</template>

<script>
	export default {
        data() {
            return {
                Nameinput: '',
                Passwdinput: ''
            }
        },
        methods:{
        	login:function(){
        		var _this = this;
        		var loginBool = false;
        		(this.Nameinput ==""||this.Passwdinput =="") ? this.toastNone("请输入用户名或密码"): loginBool=true;
        		
        		if(loginBool){
        			if(window.localStorage.usermessage){
        				var exist = false;
        				var usermessageArry = JSON.parse(window.localStorage.usermessage);
        				var usermessageArryLen = usermessageArry.length;
        				// 遍地localStorage 用户列表
        				usermessageArry.forEach(function(value,index){
        					if(value.userName==_this.Nameinput){
        						exist = true;
        					}
        				});
        				// 判断当前填写的用户名 是否存在，存在返回登录，不存在注册并写入
        				if(exist){
        					this.toastNone("已存在的用户名！请返回登录");
        				}else{
        					usermessageArry.push({
								"userName":this.Nameinput,"password":this.Passwdinput
							});
							window.localStorage.usermessage = JSON.stringify(usermessageArry);
							window.localStorage.setItem("current",_this.Nameinput);
							this.toastNone("注册成功！1秒后返回首页");
		        			setTimeout(function(){
		        				_this.$router.push({name: 'index'});
		        			},1500);
        				}
        			}else{
        				//本地localStorage不存在，即第一次使用
        				var usermessageArry = [];
        				usermessageArry.push({
        					"userName":this.Nameinput,"password":this.Passwdinput
        				});
        				window.localStorage.usermessage = JSON.stringify(usermessageArry);
        				window.localStorage.setItem("current",_this.Nameinput);
	        			this.toastNone("注册成功！1秒后返回首页");
	        			setTimeout(function(){
	        				_this.$router.push({name: 'index'});
	        			},1500);
        			}
        			
        		}
        	},
        	toastNone(msg) {
                this.$dialog.toast({
                    mes: msg,
                    timeout: 1000
                });
           }
        }
    }
</script>

<style>
</style>


