<template>
	<div class="myCenter" id="myCenter">
		<div class="header">
			<div class="pic-box">
				<img class="pic" src="https://www.xingyatrip.com/file/wap/images/banner.jpg" alt="" />
				<p class="nickname" v-if="login">{{nickname}}</p>
				<router-link class="nickname" :to="{name:'login_regiser'}" v-else>登录/注册</router-link>
			</div>
			
		</div>
		<div class="state">
			<template>
			    <yd-grids-group :rows="3" title="">
			        <yd-grids-item v-for="n,key in list" :key="key" type="link" :link="{name:'order',query:{state:n.state}}">
			            <span slot="text">{{n.name}}</span>
			        </yd-grids-item>
			    </yd-grids-group>
			</template>
		</div>
		<template>
		    <yd-cell-group>
		        <yd-cell-item arrow type="link" :href="{name:'order'}">
		        	<yd-icon slot="icon" name="order" size=".42rem"></yd-icon>
		            <span slot="left">我的订单</span>
		            <span slot="right"></span>
		        </yd-cell-item>
		        <yd-cell-item arrow type="link" :href="{name:'collect'}">
		        	<yd-icon name="collect" custom slot="icon" size=".35rem"></yd-icon>
		            <span slot="left">我的收藏</span>
		            <span slot="right"></span>
		        </yd-cell-item>
		        <yd-cell-item arrow type="link" :href="{name:'address'}">
		        	<yd-icon name="site" custom slot="icon" size=".42rem"></yd-icon>
		            <span slot="left">我的地址</span>
		            <span slot="right"></span>
		        </yd-cell-item>
		    </yd-cell-group>
		</template>
		<yd-button-group class="exitBth">
        	<yd-button class="exit" size="large" @click.native="exit">退出当前账号</yd-button>
  		</yd-button-group>
		<footertabbar></footertabbar>
	</div>
	
</template>

<script>
	import footertabbar from "../components/footer"
	export default{
		name: "myCenter",
		data(){
			return{
				login:false,
				nickname:"",
				list:[{
					name:"待发货",
					state:2
				},{
					name:"已发货",
					state:1
				},{
					name:"已签收",
					state:3
				}]
			}
		},
		mounted:function(){
			var nickname = window.localStorage.getItem("current");
			if(nickname){
				this.login = true;
				this.nickname = nickname;
			}
		},
		methods:{
			exit:function(){
				this.login = false;
				this.nickname = "";
				window.localStorage.getItem("current","");
				this.$router.push({name:"login"});
			}
		},
		components: { footertabbar }
	}
</script>

<style>
</style>


