<template>
	<div class="index" id="index">
		<yd-slider autoplay="3000" class="banner">
	        <yd-slider-item v-for="ban,key in bannBar" :key="key">
	            <router-link :to="{ name: ban.linkName}">
	            	<img :src="ban.url">
	            </router-link>
        	</yd-slider-item>
    	</yd-slider>
    	<template>
    		<p class="title_tips">每日推荐</p>
		    <yd-grids-group :rows="3" title="">
		        <yd-grids-item v-for="n,key in recommend" :key="key">
		            <span slot="text">{{n}}</span>
		        </yd-grids-item>
		    </yd-grids-group>
		</template>
		<template>
			<p class="title_tips">每日精选</p>
		    <yd-list theme="4">
		        <yd-list-item v-for="item,key in list" :key="key" type="link" :href="{name: 'details',query:{'goodsId':key}}">
		        	<img slot="img" :src="item.img">
	        		<span slot="title">{{item.title}}{{key}}</span>
	        		<yd-list-other slot="other">
	        			<div>
	        				<span class="demo-list-del-price">¥{{item.w_price}}</span>
	        				<span class="demo-list-price"><em>¥</em>{{item.price}}</span>
	        			</div>
	        			<div>热销中</div>
	        		</yd-list-other>
		        </yd-list-item>
		    </yd-list>
		</template>
		<footertabbar></footertabbar>
	</div>
</template>

<script>
	import footertabbar from "../components/footer"
	export default {
		name: 'index',
		data(){
			return {
				bannBar:[
					{
						"linkName":"myCenter",
						"url":"//gw.alicdn.com/imgextra/TB2RELduNGYBuNjy0FnXXX5lpXa_!!119-0-lubanu.jpg"
					},
					{
						"linkName":"shopCart",
						"url":"https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg"
					},
					{
						"linkName":"market1",
						"url":"https://image.missfresh.cn/8596898334d24a499bfa5efb286a5c0f.jpg?iopcmd=thumbnail&type=4&width=200"
					}
				],
				recommend:["美食","水果","男装","女装","饮品","其他"],
				list: [
                    {img: "//img1.shikee.com/try/2016/06/23/14381920926024616259.jpg", title: "春夏爆款-收腰显瘦", price: 156.23, w_price: 89.36},
                    {img: "//img1.shikee.com/try/2016/06/21/10172020923917672923.jpg", title: "春夏爆款-潮流前线", price: 256.23, w_price: 89.36},
                    {img: "//img1.shikee.com/try/2016/06/23/15395220917905380014.jpg", title: "春夏爆款-时尚主题", price: 356.23, w_price: 89.36},
                    {img: "//img1.shikee.com/try/2016/06/25/14244120933639105658.jpg", title: "春夏爆款-韩版", price: 456.23, w_price: 89.36}
                ]
			}
		},
		mounted:function(){
			var Mock = require('mockjs')
			var Random = Mock.Random;
			var data = Mock.mock({
			    // 属性 list 的值是一个数组，其中含有 1 到 10 个元素
			    'list|1-10': [{
			        // 属性 id 是一个自增数，起始值为 1，每次增 1
			        'id|+1': 1,
			        '是否勾选': Random.boolean()
			    }]
			})
			// 输出结果
			console.log(data.list)
		},
		components: { footertabbar }
//		methods:{
//			show:function(index){
//				console.log(index)
//			}
//		}
	}
</script>

<style>
</style>


